.countrypage {
  overflow: hidden; /* Hide scrollbars */
  background: black;
  color: white;
  text-align: center;
  font-size: large;
}

.nameandflag {
  float: left;
  margin-left: auto;
  margin-right: auto;
  width: 20%;
  height: 15%;
  font-size: 1.5em;
}

.totals,
.million {
  text-align: left;
  font-size: 1.5em;
  float: left;
  margin-left: 20%;
}

.rainbow {
  background-color: #343a40;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  padding: 8px 16px;
}

.rainbow-1:hover {
  background-image: linear-gradient(
    to right,
    #e7484f,
    #f68b1d,
    #fced00,
    #009e4f,
    #00aac3,
    #732982
  );
  -webkit-animation: slidebg 2s linear infinite;
          animation: slidebg 2s linear infinite;
}

@-webkit-keyframes slidebg {
  to {
    background-position: 20vw;
  }
}

@keyframes slidebg {
  to {
    background-position: 20vw;
  }
}

.graficviewcountry {
  min-height: 350px;
  min-width: 350px;
  height: 46vh;
  width: 75vw;
  overflow: hidden;
}

.tablist {
  text-shadow: #000 0.2em 0.3em 0.2em;
  font-size: 16px;
  font-weight: bold;
}

.countrydiv {
  padding: 10px;
  font-size: 18px;
}

.countryflag {
  width: 20%;
  height: 4%;
  position: absolute;
  right: 5%;
}

.separators {
  background-color: whitesmoke;
  height: 95vh;
  width: 400px;
  float: left;
  position: relative;
  margin-bottom: 20px;
  overflow: -moz-hidden-unscrollable;
}

.tab {
  text-align: center;
}

.searchandup {
  height: 45px;
}

.arrow {
  width: 45px;
}

.search {
  height: 42px;
  width: 80%;
  font-size: 16px;
  border: 1px solid #ddd;
  margin-left: 1px;
}

.arrowbutton {
  height: 45px;
  margin-left: 2%;
}

.countrylist {
  background-color: whitesmoke;
  height: 77vh;
  width: 400px;
  float: left;
  position: relative;
  margin-top: 5px;
  margin-bottom: 20px;
  overflow: auto;
}

.mapview {
  height: 500px;
}

.mainbody {
  display: inline-flex;
}

.view {
  position: relative;
}

.mapview,
.graficview {
  background-color: whitesmoke;
  margin-left: 1.5%;
  min-height: 350px;
  min-width: 350px;
  height: 46vh;
  width: 75vw;
  overflow: hidden;
  float: left;
  position: relative;
}

.graficview {
  margin-top: 1.95%;
}

.repository {
  color: white;
  margin-left: 2%;
}

.nopage {
  overflow: hidden; /* Hide scrollbars */
  background: black;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: xx-large;
}

@-webkit-keyframes glow {
  50% {
    border-color: lime;
    width: 95%;
    box-shadow: 0px 0px 10px -2px green;
  }
  100% {
    border-color: green;
    width: 35%;
    box-shadow: 0px 0px 10px -2px green;
  }
}

@keyframes glow {
  50% {
    border-color: lime;
    width: 95%;
    box-shadow: 0px 0px 10px -2px green;
  }
  100% {
    border-color: green;
    width: 35%;
    box-shadow: 0px 0px 10px -2px green;
  }
}

.sep {
  width: 35%;
  border-bottom: 2px solid green;
  margin-bottom: 20px;
  -webkit-animation: glow 8s infinite;
          animation: glow 8s infinite;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: black;
  width: 75%;
  padding: 10%;
  flex-wrap: wrap;
}

.loader {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 20px;
}

@-webkit-keyframes load {
  50% {
    transform: rotatez(180deg) scale(0.2);
    border-color: orange;
  }
  100% {
    transform: rotatez(360deg) scale(1.1);
  }
}

@keyframes load {
  50% {
    transform: rotatez(180deg) scale(0.2);
    border-color: orange;
  }
  100% {
    transform: rotatez(360deg) scale(1.1);
  }
}

.loader1::before {
  content: "";
  color: white;
  height: 30px;
  width: 30px;
  background: transparent;
  border-radius: 45%;
  border: 25px dotted lime;
  -webkit-animation: load 1s infinite ease-in-out;
          animation: load 1s infinite ease-in-out;
}

@-webkit-keyframes load2 {
  50% {
    transform: rotatez(180deg);
    border-radius: 55%;
  }
  100% {
    transform: rotatez(360deg);
  }
}

@keyframes load2 {
  50% {
    transform: rotatez(180deg);
    border-radius: 55%;
  }
  100% {
    transform: rotatez(360deg);
  }
}

.loader2::before {
  content: "";
  color: white;
  height: 30px;
  width: 30px;
  background: transparent;
  border-radius: 10%;
  border: 25px inset orange;
  -webkit-animation: load2 1s infinite ease-in-out;
          animation: load2 1s infinite ease-in-out;
}

@-webkit-keyframes load3 {
  50% {
    transform: rotatez(180deg) scale(1.5);
    border-style: dotted;
  }
  100% {
    transform: rotatez(360deg) scale(0.9);
  }
}

@keyframes load3 {
  50% {
    transform: rotatez(180deg) scale(1.5);
    border-style: dotted;
  }
  100% {
    transform: rotatez(360deg) scale(0.9);
  }
}

.loader3::before {
  content: "";
  color: white;
  height: 30px;
  width: 30px;
  background: transparent;
  border-radius: 50%;
  border: 15px solid blue;
  border-color: #1565c0 #26c6da;
  -webkit-animation: load3 2s infinite;
          animation: load3 2s infinite;
}

@-webkit-keyframes load4 {
  50% {
    transform: rotatez(180deg);
    border-width: 30px;
  }
  100% {
    transform: rotatez(360deg);
  }
}

@keyframes load4 {
  50% {
    transform: rotatez(180deg);
    border-width: 30px;
  }
  100% {
    transform: rotatez(360deg);
  }
}

.loader4::before {
  content: "";
  color: white;
  height: 30px;
  width: 30px;
  background: transparent;
  border-radius: 50%;
  border: 15px ridge lime;
  border-color: lime transparent;
  -webkit-animation: load4 1s infinite;
          animation: load4 1s infinite;
}

@-webkit-keyframes load5 {
  40% {
    transform: rotatez(-180deg);
    border-width: 16px;
  }
  80% {
    transform: rotatez(-360deg);
  }
  100% {
    transform: rotatez(-360deg);
  }
}

@keyframes load5 {
  40% {
    transform: rotatez(-180deg);
    border-width: 16px;
  }
  80% {
    transform: rotatez(-360deg);
  }
  100% {
    transform: rotatez(-360deg);
  }
}

.loader5::before {
  content: "";
  color: white;
  height: 50px;
  width: 50px;
  background: transparent;
  border-radius: 50%;
  border: 10px dotted lime;
  border-left-color: transparent;
  -webkit-animation: load5 1s infinite ease-in-out;
          animation: load5 1s infinite ease-in-out;
}

@-webkit-keyframes load6 {
  50% {
    transform: rotatez(180deg);
    border-width: 40px;
  }
  100% {
    transform: rotatez(360deg);
  }
}

@keyframes load6 {
  50% {
    transform: rotatez(180deg);
    border-width: 40px;
  }
  100% {
    transform: rotatez(360deg);
  }
}

.loader6::before {
  content: "";
  color: white;
  height: 0px;
  width: 0px;
  background: transparent;
  border-radius: 50%;
  border: 35px ridge red;
  border-color: indigo lime yellow orangered;
  -webkit-animation: load6 0.5s infinite;
          animation: load6 0.5s infinite;
}

@-webkit-keyframes load7 {
  100% {
    transform: rotatez(360deg);
  }
}

@keyframes load7 {
  100% {
    transform: rotatez(360deg);
  }
}

.loader7::before {
  content: "";
  color: white;
  height: 50px;
  width: 50px;
  background: transparent;
  border-radius: 50%;
  border: 10px solid blue;
  border-color: #0277bd #0277bd #0277bd #81d4fa;
  -webkit-animation: load7 0.6s infinite ease-in-out;
          animation: load7 0.6s infinite ease-in-out;
  box-shadow: 0px 0px 40px -2px skyblue;
}

@-webkit-keyframes load8 {
  100% {
    transform: rotatez(360deg);
  }
}

@keyframes load8 {
  100% {
    transform: rotatez(360deg);
  }
}

.loader8::before {
  content: "";
  color: white;
  height: 50px;
  width: 50px;
  background: transparent;
  border-radius: 50%;
  border: 10px solid transparent;
  border-color: #039be5 #039be5 transparent transparent;
  -webkit-animation: load8 0.6s infinite;
          animation: load8 0.6s infinite;
}

@-webkit-keyframes load9 {
  50% {
    transform: rotatez(180deg) scale(0.6);
  }
  100% {
    transform: rotatez(360deg) scale(1);
  }
}

@keyframes load9 {
  50% {
    transform: rotatez(180deg) scale(0.6);
  }
  100% {
    transform: rotatez(360deg) scale(1);
  }
}

.loader9::before {
  content: "";
  color: white;
  height: 50px;
  width: 50px;
  background: transparent;
  border-radius: 50%;
  border: 20px dashed transparent;
  border-color: #039be5 indianred;
  -webkit-animation: load9 0.6s infinite ease-in-out;
          animation: load9 0.6s infinite ease-in-out;
}

@-webkit-keyframes load10 {
  50% {
    transform: rotatez(180deg);
    border-style: dashed;
    border-color: red maroon indianred orangered;
  }
  100% {
    transform: rotatez(360deg);
  }
}

@keyframes load10 {
  50% {
    transform: rotatez(180deg);
    border-style: dashed;
    border-color: red maroon indianred orangered;
  }
  100% {
    transform: rotatez(360deg);
  }
}

.loader10::before {
  content: "";
  color: white;
  height: 50px;
  width: 50px;
  background: transparent;
  border-radius: 50%;
  border: 20px solid red;
  -webkit-animation: load10 1s infinite;
          animation: load10 1s infinite;
}

.returnbutton {
  display: inline-block;
  padding: 0.3em 1.2em;
  margin: 0 0.1em 0.1em 0;
  border: 0.16em solid rgba(255, 255, 255, 0);
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  text-align: center;
  transition: all 0.2s;
}

button.returnbutton:hover {
  border-top-color: rgba(255, 255, 255, 1);
}

@media all and (max-width: 30em) {
  .returnbutton {
    display: block;
    margin: 0.2em auto;
  }
}

body {
  margin: 1%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

